/* eslint-disable @typescript-eslint/no-expliciT-any */
const API = process.env.NEXT_PUBLIC_API_URL;

export async function generateUI(
  command: string,
  sessionId?: string
): Promise<any> {
  const res = await fetch(`${API}/generate-ui`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      command,
      session_id: sessionId,
      require_verification: true,
      stream_response: false
    })
  });

  if (!res.ok) throw new Error(await res.text());
  return res.json();
}

export function generateUIStream(
  command: string,
  sessionId: string,
  onMessage: (msg: any) => void,
  onError?: (e: any) => void
) {
  const ws = new WebSocket(`${process.env.NEXT_PUBLIC_WS_URL}/${sessionId}`);

  ws.onopen = () =>
    ws.send(JSON.stringify({ type: 'generate_ui', command }));

  ws.onmessage = evt => {
    try {
      onMessage(JSON.parse(evt.data));
    } catch {
      /* swallow JSON parse errors */
    }
  };

  ws.onerror = onError ?? (() => {});
  return ws; // caller may close it
}

export async function saveTemplate(payload: {
  html: string;
  parent_template_id: string;
  user: string;
  confidence_score?: number;
  feedback_rating?: number;
}) {
  const res = await fetch(`${API}/save-ui`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(payload)
  });
  if (!res.ok) throw new Error(await res.text());
  return res.json();
}

export async function submitFeedback(payload: {
  template_id: string;
  user_id: string;
  rating: number;
  feedback_text?: string;
  is_helpful: boolean;
}) {
  const res = await fetch(`${API}/feedback`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(payload)
  });
  if (!res.ok) throw new Error(await res.text());
  return res.json();
}

export async function fetchAnalytics() {
  const res = await fetch(`${API}/analytics`);
  if (!res.ok) throw new Error(await res.text());
  return res.json();
}
